import { gql } from "@apollo/client";

export const STUDENT_REPORT = gql `
    query studentReport ($studentCode: String) {
        studentReport (studentCode: $studentCode) {
            school
            textTop
            textMid
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const STUDENT_REPORT_SECTION = gql `
    query studentReportSection ($section: Int) {
        studentReportSection (section: $section) {
            school
            textTop
            textMid
            textBottom
            studentPhoto
            studentCode
        }
    }
`;

export const ALL_SECTIONS = gql `
    query allSections {
        allSections {
            id
            section
        }
    }
`;


export const STUDENT_SCHOOL_YEAR = gql `
    query studentSchoolyear ($student: Int) {
        studentSchoolyear (student: $student) {
            id
            schoolyear
            season
            semesterCode
            startDate
            endDate
        }
    }
`;


export const ALL_STUDENTS_REPORT = gql`
    query allStudentsReport {
        allStudentsReport {
            id
            user {
                id
                username
                email
            }
            studentCode
            surname
            familyName
            name
            religion
            registerNo
            nationality
            state
            photo
            phone
            address
            activity {
                id
                name
            }
            joinDate
            sex
            birthdate
            classtime {
                id
                name
            }
            birthCity {
                id
                name
            }
            birthDistrict {
                id
                name
            }
            status {
                id
                name
            }
            statusExtra {
                id
                name
            }
            school {
                id
                name
            }
            classes {
                id
                classes
            }
            section {
                id
                section
            }
            program {
                id
                program
            }
            joinSchoolyear {
                id
                schoolyear
            }
        }
    }
`;